@import '../variables';

// Payroll Calculator Page
.payroll-calculator-page {
    .crew-member-info {
        background: $color-gray-light-3;
        padding: 1rem;
        border-radius: $border-radius;
        margin-top: 1rem;

        h6 {
            margin: 0 0 1rem 0;
            color: $color-primary;
            font-weight: 600;
        }

        .info-grid {
            display: grid;
            gap: 0.5rem;

            .info-item {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 0.5rem;
                background: $color-white;
                border-radius: $border-radius-small;
                border: 1px solid $color-gray-light-2;

                .label {
                    font-weight: 500;
                    color: $color-gray;
                }

                .value {
                    font-weight: 600;
                    color: $color-black;
                }
            }
        }
    }

    .empty-state {
        text-align: center;
        padding: 3rem 1rem;

        .empty-icon {
            font-size: 3rem;
            color: $color-gray-light;
            margin-bottom: 1rem;
        }

        h5 {
            color: $color-gray;
            margin-bottom: 0.5rem;
        }

        p {
            color: $color-gray-light;
            margin: 0;
        }
    }

    .calculation-result {
        background: rgba($color-success, 0.1);
        border: 1px solid rgba($color-success, 0.3);
        border-radius: $border-radius;
        padding: 1rem;
        text-align: center;

        h6 {
            margin: 0;
            color: $color-success;
            font-weight: 600;
        }
    }

    // Clean Professional Card Style
    .payroll-card {
        background: $color-white;
        border-radius: $border-radius;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
        padding: 1.5rem;
        margin-bottom: 1rem;
        height: 100%;
        display: flex;
        flex-direction: column;

        &.deductions-card {
            background: rgba($color-warning, 0.02);
            border: 2px solid $color-warning;
        }

        &.bonuses-card {
            background: rgba($color-success-light, 0.3);
            border: 2px solid $color-success;
        }

        .card-header {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            margin-bottom: 0.5rem;

            .header-left {
                display: flex;
                align-items: center;
                gap: 1rem;
                width: 100%;

                .header-icon {
                    width: 48px;
                    height: 48px;
                    border-radius: 12px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 1.5rem;
                    color: $color-white;
                    font-weight: 600;
                }

                .header-content {
                    flex: 1;

                    .header-title {
                        font-size: 1.25rem;
                        font-weight: 700;
                        color: $color-black;
                        margin: 0 0 0.25rem 0;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        gap: 1rem;

                        span {
                            flex: 1;
                        }

                        .status-chip {
                            display: inline-flex;
                            align-items: center;
                            gap: 0.5rem;
                            padding: 0.25rem 0.75rem;
                            border-radius: 20px;
                            font-size: 0.75rem;
                            font-weight: 600;
                            color: $color-white;
                            flex-shrink: 0;

                            &.status-completed {
                                background: $color-primary;
                            }

                            &.status-pending {
                                background: $color-warning;
                            }

                            &.status-active {
                                background: $color-success;
                            }

                            &.status-inactive {
                                background: $color-danger;
                            }
                        }
                    }

                    .header-subtitle {
                        font-size: 0.875rem;
                        color: $color-gray;
                        margin: 0;
                    }

                    .header-details {
                        font-size: 0.75rem;
                        color: $color-gray-light;
                        margin: 0.25rem 0 0 0;
                    }
                }
            }
        }

        .card-content {
            flex: 1;
            display: flex;
            flex-direction: column;

            .purpose-section {
                margin-bottom: 1.5rem;

                .purpose-label {
                    font-weight: 600;
                    color: $color-black;
                    margin-bottom: 0.5rem;
                }

                .purpose-text {
                    color: $color-gray;
                    margin: 0;
                }
            }

            .details-section {
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
                gap: 1rem;
                margin-bottom: 1.5rem;

                .detail-item {
                    .detail-label {
                        font-weight: 600;
                        color: $color-black;
                        font-size: 0.875rem;
                        margin-bottom: 0.25rem;
                    }

                    .detail-value {
                        color: $color-gray;
                        font-size: 0.875rem;
                        margin: 0;
                    }
                }
            }

            .action-buttons {
                display: flex;
                gap: 0.75rem;
                align-items: center;

                .btn-view {
                    background: $color-gray-light-3;
                    border: 1px solid $color-gray-light-2;
                    color: $color-black;
                    padding: 0.5rem 1rem;
                    border-radius: $border-radius;
                    font-size: 0.875rem;
                    font-weight: 500;
                    display: flex;
                    align-items: center;
                    gap: 0.5rem;
                    text-decoration: none;
                    transition: all 0.2s ease;

                    &:hover {
                        background: $color-gray-light-2;
                        text-decoration: none;
                        color: $color-black;
                    }
                }

                .btn-download {
                    background: $color-success;
                    border: 1px solid $color-success;
                    color: $color-white;
                    padding: 0.5rem 1rem;
                    border-radius: $border-radius;
                    font-size: 0.875rem;
                    font-weight: 500;
                    display: flex;
                    align-items: center;
                    gap: 0.5rem;
                    text-decoration: none;
                    transition: all 0.2s ease;

                    &:hover {
                        background: darken($color-success, 10%);
                        text-decoration: none;
                        color: $color-white;
                    }
                }
            }
        }

        // Specific card types
        &.crew-selection-card {
            .header-icon {
                background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
            }
        }

        &.commission-calculator-card {
            .header-icon {
                background: linear-gradient(135deg, #3498db 0%, #2980b9 100%);
            }
        }

        &.salary-summary-card {
            .header-icon {
                background: linear-gradient(135deg, #9b59b6 0%, #8e44ad 100%);
            }
        }

        &.deductions-card {
            .header-icon {
                background: linear-gradient(135deg, #ff9f43 0%, #f39c12 100%);
            }
        }

        &.bonuses-card {
            .header-icon {
                background: linear-gradient(135deg, #00b894 0%, #00a085 100%);
            }
        }
    }

    .salary-summary {
        .summary-item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0.75rem 0;
            border-bottom: 1px solid $color-gray-light-3;

            &:last-child {
                border-bottom: none;
            }

            &.total {
                border-top: 2px solid $color-primary;
                border-bottom: none;
                font-weight: 600;
                color: $color-primary;
                font-size: 1.1rem;
            }

            .label {
                color: $color-gray;
                font-weight: 500;
            }

            .value {
                font-weight: 600;
                color: $color-black;
            }
        }
    }

    .deductions-list,
    .bonuses-list {
        max-height: 200px;
        overflow-y: auto;
        margin-bottom: 1rem;
        padding: 0.5rem 0;

        .deduction-item,
        .bonus-item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0.75rem;
            background: $color-white;
            border: 1px solid $color-gray-light-2;
            border-radius: $border-radius-small;
            margin-bottom: 0.5rem;
            transition: all 0.2s ease;

            &:last-child {
                margin-bottom: 0;
            }

            &:hover {
                transform: translateY(-1px);
                box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
            }

            .deduction-info,
            .bonus-info {
                flex: 1;

                strong {
                    display: block;
                    color: $color-black;
                    font-weight: 600;
                    margin-bottom: 0.2rem;
                }

                .amount {
                    color: $color-gray;
                    font-size: 0.9rem;
                }
            }

            .delete-btn {
                opacity: 0.6;
                transition: opacity 0.2s ease;

                &:hover {
                    opacity: 1;
                }
            }
        }
    }

    .add-deduction-form,
    .add-bonus-form {
        background: $color-gray-light-3;
        padding: 1rem;
        border-radius: $border-radius;
        border: 1px solid $color-gray-light-2;

        h6 {
            margin: 0 0 1rem 0;
            color: $color-black;
            font-weight: 600;
            display: flex;
            align-items: center;
            gap: 0.5rem;

            &::before {
                content: '';
                width: 4px;
                height: 16px;
                border-radius: 2px;
            }
        }

        &.deduction-form h6::before {
            background: linear-gradient(135deg, #ff9f43 0%, #f39c12 100%);
        }

        &.bonus-form h6::before {
            background: linear-gradient(135deg, #00b894 0%, #00a085 100%);
        }
    }
}

// Payslip Modal Styles
.payslip-modal {
    .payslip-content {
        .payslip-header {
            text-align: center;
            margin-bottom: 2rem;
            padding-bottom: 1rem;
            border-bottom: 2px solid $color-gray-light-2;

            h4 {
                margin: 0 0 1rem 0;
                color: $color-primary;
                font-weight: 600;
            }

            .payslip-meta {
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
                gap: 1rem;

                p {
                    margin: 0;
                    color: $color-gray;
                    font-size: 0.9rem;

                    strong {
                        color: $color-black;
                        font-weight: 600;
                    }
                }
            }
        }

        .payslip-details {

            .earnings-section,
            .deductions-section,
            .net-salary-section {
                margin-bottom: 2rem;

                h5 {
                    margin: 0 0 1rem 0;
                    color: $color-black;
                    font-weight: 600;
                    padding-bottom: 0.5rem;
                    border-bottom: 1px solid $color-gray-light-2;
                }
            }

            .earnings-item,
            .deduction-item {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 0.75rem 0;
                border-bottom: 1px solid $color-gray-light-3;

                &:last-child {
                    border-bottom: none;
                }

                &.total {
                    border-top: 2px solid $color-primary;
                    border-bottom: none;
                    font-weight: 600;
                    color: $color-primary;
                    font-size: 1.1rem;
                }
            }

            .net-salary-section {
                background: rgba($color-success, 0.1);
                padding: 1.5rem;
                border-radius: $border-radius;
                border: 2px solid $color-success;

                .net-salary {
                    text-align: center;
                    font-size: 1.5rem;
                    font-weight: 700;
                    color: $color-success;
                }
            }
        }
    }
}

// Responsive adjustments
@media (max-width: 768px) {
    .payroll-calculator-page {
        .salary-summary {
            .summary-item {
                flex-direction: column;
                align-items: flex-start;
                gap: 0.25rem;

                .value {
                    align-self: flex-end;
                }
            }
        }

        .deductions-list,
        .bonuses-list {

            .deduction-item,
            .bonus-item {
                flex-direction: column;
                align-items: flex-start;
                gap: 0.5rem;

                .deduction-info,
                .bonus-info {
                    width: 100%;
                }
            }
        }
    }

    .payslip-modal {
        .payslip-content {
            .payslip-header {
                .payslip-meta {
                    grid-template-columns: 1fr;
                }
            }

            .payslip-details {

                .earnings-item,
                .deduction-item {
                    flex-direction: column;
                    align-items: flex-start;
                    gap: 0.25rem;
                }
            }
        }
    }
}

// Mobile responsive styles for Payroll Calculator
@media (max-width: 768px) {
    .payroll-calculator-page {
        .payroll-content {
            flex-direction: column;
            gap: 1rem;

            .calculator-section {
                order: 1;
                width: 100%;

                .calculator-header {
                    flex-direction: column;
                    gap: 0.75rem;
                    align-items: stretch;
                    margin-bottom: 1rem;

                    h3 {
                        font-size: 1.1rem;
                        text-align: center;
                    }

                    .header-actions {
                        justify-content: center;
                        flex-wrap: wrap;
                        gap: 0.5rem;

                        .btn-root {
                            padding: 0.5rem 0.75rem;
                            font-size: 0.8rem;
                        }
                    }
                }

                .calculator-form {
                    .form-row {
                        flex-direction: column;
                        gap: 0.75rem;

                        .form-group {
                            width: 100%;
                        }
                    }

                    .form-group {
                        margin-bottom: 1rem;

                        label {
                            font-size: 0.875rem;
                            margin-bottom: 0.375rem;
                        }

                        .input-group {
                            .input-field {
                                padding: 0.75rem;
                                font-size: 0.875rem;
                            }
                        }
                    }
                }

                .deductions-section,
                .bonuses-section {
                    margin-top: 1rem;

                    .section-header {
                        flex-direction: column;
                        gap: 0.5rem;
                        align-items: stretch;
                        margin-bottom: 0.75rem;

                        h4 {
                            font-size: 1rem;
                            text-align: center;
                        }

                        .add-button {
                            width: 100%;
                            justify-content: center;
                            padding: 0.5rem;
                            font-size: 0.8rem;
                        }
                    }

                    .items-list {
                        gap: 0.5rem;

                        .item-card {
                            padding: 0.75rem;
                            flex-direction: column;
                            gap: 0.5rem;
                            align-items: stretch;

                            .item-info {
                                order: 1;
                                text-align: center;

                                .item-name {
                                    font-size: 0.875rem;
                                    margin-bottom: 0.25rem;
                                }

                                .item-amount {
                                    font-size: 1rem;
                                }
                            }

                            .item-actions {
                                order: 2;
                                justify-content: center;
                                gap: 0.5rem;

                                .btn-root {
                                    padding: 0.375rem 0.5rem;
                                    font-size: 0.75rem;
                                }
                            }
                        }
                    }
                }
            }

            .summary-section {
                order: 2;
                width: 100%;

                .summary-header {
                    flex-direction: column;
                    gap: 0.75rem;
                    align-items: stretch;
                    margin-bottom: 1rem;

                    h3 {
                        font-size: 1.1rem;
                        text-align: center;
                    }
                }

                .salary-breakdown {
                    gap: 0.75rem;

                    .breakdown-item {
                        padding: 0.75rem;
                        flex-direction: column;
                        gap: 0.5rem;
                        align-items: center;
                        text-align: center;

                        .item-label {
                            font-size: 0.875rem;
                            margin-bottom: 0.25rem;
                        }

                        .item-value {
                            font-size: 1.25rem;
                        }
                    }

                    .total-item {
                        padding: 1rem;
                        font-size: 1.1rem;
                    }
                }

                .payslip-section {
                    margin-top: 1rem;

                    .payslip-header {
                        flex-direction: column;
                        gap: 0.5rem;
                        align-items: stretch;
                        margin-bottom: 0.75rem;

                        h4 {
                            font-size: 1rem;
                            text-align: center;
                        }

                        .generate-btn {
                            width: 100%;
                            justify-content: center;
                            padding: 0.75rem;
                            font-size: 0.875rem;
                        }
                    }
                }
            }
        }
    }
}

// Tablet adjustments
@media (min-width: 769px) and (max-width: 1024px) {
    .payroll-calculator-page {
        .payroll-content {
            gap: 1.5rem;

            .calculator-section {
                .calculator-form {
                    .form-row {
                        flex-wrap: wrap;
                        gap: 1rem;

                        .form-group {
                            flex: 1;
                            min-width: 200px;
                        }
                    }
                }
            }

            .summary-section {
                .salary-breakdown {
                    .breakdown-item {
                        flex-direction: row;
                        justify-content: space-between;
                        text-align: left;
                    }
                }
            }
        }
    }
}

// Large screen optimizations
@media (min-width: 1025px) {
    .payroll-calculator-page {
        .payroll-content {
            gap: 2rem;

            .calculator-section {
                .calculator-form {
                    .form-row {
                        gap: 1.5rem;
                    }
                }
            }
        }
    }
}