@import '../variables';

// Crew Member Profile Page
.crew-member-profile-page {

    .profile-header,
    .page-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 2rem;
        padding-bottom: 1rem;
        border-bottom: 1px solid $box-border-color;

        .profile-title {
            margin: 0;
            color: $color-primary;
            font-size: 1.8rem;
            font-weight: 600;
        }

        .profile-status {
            display: flex;
            gap: 0.5rem;
            align-items: center;

            .status-badge {
                padding: 0.25rem 0.75rem;
                border-radius: $border-radius-xlarge;
                font-size: 0.8rem;
                font-weight: 500;
                display: flex;
                align-items: center;
                gap: 0.25rem;

                &.approved {
                    background-color: rgba(39, 174, 96, 0.1);
                    color: $color-success;
                    border: 1px solid rgba(39, 174, 96, 0.3);
                }

                &.pending {
                    background-color: rgba(243, 156, 18, 0.1);
                    color: $color-warning;
                    border: 1px solid rgba(243, 156, 18, 0.3);
                }

                &.rejected {
                    background-color: rgba(231, 76, 60, 0.1);
                    color: $color-danger;
                    border: 1px solid rgba(231, 76, 60, 0.3);
                }

                &.suspended {
                    background-color: rgba(155, 89, 182, 0.1);
                    color: #9b59b6;
                    border: 1px solid rgba(155, 89, 182, 0.3);
                }

                &.medical-case {
                    background-color: rgba(230, 126, 34, 0.1);
                    color: $color-warning;
                    border: 1px solid rgba(230, 126, 34, 0.3);
                    font-weight: 600;
                }
            }
        }
    }

    .profile-overview {
        margin-bottom: 2rem;

        .profile-avatar {
            text-align: center;
            margin-bottom: 1rem;

            img {
                width: 120px;
                height: 120px;
                border-radius: 50%;
                object-fit: cover;
                border: 3px solid $color-primary;
            }

            .avatar-placeholder {
                width: 120px;
                height: 120px;
                border-radius: 50%;
                background: $color-gray-light-4;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 auto;
                border: 3px solid $color-primary;

                svg {
                    font-size: 3rem;
                    color: $color-gray;
                }
            }
        }

        .profile-info {
            .info-item {
                display: flex;
                align-items: center;
                gap: 0.75rem;
                margin-bottom: 1rem;
                padding: 0.75rem;
                background: $color-gray-light-4;
                border-radius: $border-radius-small;

                svg {
                    color: $color-primary;
                    font-size: 1rem;
                }

                span {
                    color: $color-black;
                    font-weight: 500;
                }
            }
        }
    }

    .tab-content {
        .overview-tab {
            h4 {
                color: $color-primary;
                margin-bottom: 1rem;
                font-weight: 600;
            }

            .rdtb-status {
                .rdtb-item {
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;
                    margin-bottom: 0.75rem;
                    padding: 0.75rem;
                    background: $color-gray-light-4;
                    border-radius: $border-radius-small;

                    svg {
                        color: $color-primary;
                        font-size: 1.2rem;
                    }

                    span {
                        font-weight: 500;
                    }
                }
            }

            .recent-activity {
                .activity-item {
                    display: flex;
                    align-items: center;
                    gap: 0.75rem;
                    margin-bottom: 0.75rem;
                    padding: 0.75rem;
                    background: $color-gray-light-4;
                    border-radius: $border-radius-small;

                    svg {
                        color: $color-primary;
                        font-size: 1rem;
                    }

                    span {
                        color: $color-gray;
                        font-size: 0.9rem;
                    }
                }
            }

            .medical-case-section {
                background-color: rgba(230, 126, 34, 0.05);
                border: 1px solid rgba(230, 126, 34, 0.2);
                border-radius: $border-radius-small;
                padding: 1rem;
                margin: 1rem 0;
                min-height: 103px;

                .medical-case-info {
                    h5 {
                        color: $color-warning;
                        margin-bottom: 0.5rem;
                        display: flex;
                        align-items: center;
                        gap: 0.5rem;
                    }

                    p {
                        margin-bottom: 0.25rem;
                    }
                }

                .btn-warning {
                    background-color: $color-warning-hover;
                    border-color: $color-warning;
                    color: $color-white;

                    &:hover {
                        background-color: darken($color-warning, 10%);
                        border-color: darken($color-warning, 10%);
                    }
                }

                .btn-danger {
                    background-color: $color-danger-hover;
                    border-color: $color-danger;
                    color: $color-white;

                    &:hover {
                        background-color: darken($color-danger, 10%);
                        border-color: darken($color-danger, 10%);
                    }
                }
            }
        }

        .medical-tab {
            .medical-history {
                h4 {
                    color: $color-primary;
                    margin-bottom: 1.5rem;
                    font-weight: 600;
                }

                .medical-record {
                    background: $color-white;
                    border: 1px solid $box-border-color;
                    border-radius: $border-radius;
                    padding: 1.5rem;
                    margin-bottom: 1rem;
                    transition: all 0.2s;

                    &:hover {
                        box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
                    }

                    .record-header {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        margin-bottom: 1rem;
                        padding-bottom: 1rem;
                        border-bottom: 1px solid $box-border-color;

                        .record-type {
                            display: flex;
                            align-items: center;
                            gap: 0.75rem;
                            font-weight: 600;
                            color: $color-primary;

                            svg {
                                font-size: 1.2rem;
                            }
                        }

                        .record-status {
                            .status-badge {
                                padding: 0.5rem 1rem;
                                border-radius: $border-radius-xlarge;
                                font-size: 0.875rem;
                                font-weight: 500;
                                display: flex;
                                align-items: center;
                                gap: 0.5rem;

                                &.completed {
                                    background-color: rgba(39, 174, 96, 0.1);
                                    color: $color-success;
                                    border: 1px solid rgba(39, 174, 96, 0.3);
                                }

                                &.pending {
                                    background-color: rgba(243, 156, 18, 0.1);
                                    color: $color-warning;
                                    border: 1px solid rgba(243, 156, 18, 0.3);
                                }

                                &.expired {
                                    background-color: rgba(231, 76, 60, 0.1);
                                    color: $color-danger;
                                    border: 1px solid rgba(231, 76, 60, 0.3);
                                }
                            }
                        }
                    }

                    .record-content {
                        p {
                            margin-bottom: 1rem;
                            color: $color-black;
                        }

                        .record-meta {
                            display: flex;
                            gap: 2rem;
                            margin-bottom: 1rem;
                            flex-wrap: wrap;

                            span {
                                color: $color-gray;
                                font-size: 0.9rem;
                            }
                        }

                        .record-notes {
                            background: $color-gray-light-4;
                            padding: 1rem;
                            border-radius: $border-radius-small;
                            margin-top: 1rem;
                            font-style: italic;
                        }
                    }
                }
            }
        }



        .documents-tab {
            .documents-list {
                h4 {
                    color: $color-primary;
                    margin-bottom: 1.5rem;
                    font-weight: 600;
                }

                .document-item {
                    background: $color-white;
                    border: 1px solid $box-border-color;
                    border-radius: $border-radius;
                    padding: 1.5rem;
                    margin-bottom: 1rem;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    transition: all 0.2s;

                    &:hover {
                        box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
                    }

                    .document-info {
                        flex: 1;

                        .document-type {
                            display: flex;
                            align-items: center;
                            gap: 0.75rem;
                            font-weight: 600;
                            color: $color-primary;
                            margin-bottom: 0.5rem;

                            svg {
                                font-size: 1.2rem;
                            }
                        }

                        .document-details {
                            display: flex;
                            gap: 2rem;
                            flex-wrap: wrap;

                            span {
                                color: $color-gray;
                                font-size: 0.9rem;
                            }
                        }
                    }

                    .document-actions {
                        display: flex;
                        align-items: center;
                        gap: 1rem;

                        .status-badge {
                            padding: 0.5rem 1rem;
                            border-radius: $border-radius-xlarge;
                            font-size: 0.875rem;
                            font-weight: 500;
                            display: flex;
                            align-items: center;
                            gap: 0.5rem;

                            &.valid {
                                background-color: rgba(39, 174, 96, 0.1);
                                color: $color-success;
                                border: 1px solid rgba(39, 174, 96, 0.3);
                            }

                            &.pending {
                                background-color: rgba(243, 156, 18, 0.1);
                                color: $color-warning;
                                border: 1px solid rgba(243, 156, 18, 0.3);
                            }

                            &.expiring_soon {
                                background-color: rgba(230, 126, 34, 0.1);
                                color: $color-warning;
                                border: 1px solid rgba(230, 126, 34, 0.3);
                            }

                            &.expired {
                                background-color: rgba(231, 76, 60, 0.1);
                                color: $color-danger;
                                border: 1px solid rgba(231, 76, 60, 0.3);
                            }
                        }
                    }
                }
            }
        }
    }
}