@import '../variables.scss';

.support-widget-root {
    position: fixed;
    bottom: 32px;
    right: 32px;
    z-index: 2000;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.support-fab {
    background: linear-gradient(135deg, #016071, #4ad991);
    color: $color-white;
    border: none;
    border-radius: 50%;
    width: 64px;
    height: 64px;
    font-size: 2rem;
    box-shadow: 0 4px 24px rgba(1, 96, 113, 0.18);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background 0.2s, transform 0.2s;
}

.support-fab:hover {
    background: linear-gradient(135deg, #4ad991, #016071);
    transform: scale(1.07);
}

.support-popover {
    position: absolute;
    bottom: 80px;
    right: 0;
    width: 340px;
    background: $color-white;
    border-radius: 18px;
    box-shadow: 0 8px 32px rgba(1, 96, 113, 0.18);
    padding: 1.5rem 1.2rem 1.2rem 1.2rem;
    z-index: 2100;
    animation: fadeInSupport 0.25s;
}

@keyframes fadeInSupport {
    from {
        opacity: 0;
        transform: translateY(30px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.support-close {
    position: absolute;
    top: 12px;
    right: 12px;
    background: none;
    border: none;
    color: #016071;
    font-size: 1.2rem;
    cursor: pointer;
    border-radius: 50%;
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 0.2s;
}

.support-close:hover {
    background: #f0f4f8;
}

.support-popover h4 {
    margin: 0 0 1rem 0;
    font-size: 1.15rem;
    color: #016071;
    font-weight: 700;
}

.support-options {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 0.7rem;
}

.support-options li button {
    width: 100%;
    background: #f7fafc;
    border: none;
    border-radius: 8px;
    padding: 0.7rem 1rem;
    color: #016071;
    font-size: 1rem;
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: 0.7rem;
    cursor: pointer;
    transition: background 0.18s, color 0.18s;
}

.support-options li button:hover {
    background: #e6f7f1;
    color: #4ad991;
}

.faq-section {
    background: #f7fafc;
    border-radius: 8px;
    padding: 0.7rem 1rem 0.7rem 1rem;
    color: #016071;
    font-size: 1rem;
    font-weight: 500;
    display: flex;
    align-items: flex-start;
    gap: 0.7rem;
    flex-direction: column;
}

.faq-list {
    list-style: disc inside;
    margin: 0.5rem 0 0 1.2rem;
    padding: 0;
    color: #016071;
    font-size: 0.97rem;
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
}

.faq-list a {
    color: #016071;
    text-decoration: underline;
    transition: color 0.18s;
}

.faq-list a:hover {
    color: #4ad991;
}

.ai-assistant {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 1rem 0 0 0;
}

.avatar-placeholder {
    width: 200px;
    height: 300px;
    border-radius: 50%;
    background: linear-gradient(135deg, #e6f7f1, #01607122);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
    box-shadow: 0 2px 12px rgba(1, 96, 113, 0.10);
    animation: avatarPop 0.5s;

    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.avatar-icon {
    font-size: 3rem;
    color: #016071;
    animation: avatarWave 1.2s infinite alternate;
}

@keyframes avatarPop {
    from {
        transform: scale(0.7);
        opacity: 0;
    }

    to {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes avatarWave {
    from {
        transform: rotate(-8deg);
    }

    to {
        transform: rotate(8deg);
    }
}

.assistant-content h5 {
    margin: 0 0 0.5rem 0;
    color: #016071;
    font-size: 1.1rem;
    font-weight: 700;
}

.assistant-content p {
    color: #4a4a4a;
    font-size: 1rem;
    margin-bottom: 1.2rem;
}

.start-talking-btn {
    background: linear-gradient(135deg, #016071, #4ad991);
    color: $color-white;
    border: none;
    border-radius: 8px;
    padding: 0.7rem 1.5rem;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(1, 96, 113, 0.10);
    transition: background 0.18s, transform 0.18s;
}

.start-talking-btn:hover {
    background: linear-gradient(135deg, #4ad991, #016071);
    transform: scale(1.05);
}