@import 'variables';

.text-title {
    font-size: 1.5rem;
    font-weight: 500;
    color: $color-black;
    margin-bottom: 1rem;
    font-family: $font-family-title;
    text-transform: uppercase;
    letter-spacing: 0.01em;
}

h2.title {
    font-size: 1.5rem;
    font-weight: 600;
    color: $color-primary;
    margin: 0 0 0.5rem 0;
    font-family: $font-family-title;
    text-transform: uppercase;
    // letter-spacing: 1em;
}

.download-link {
    font-size: 1.05rem;
    color: $color-primary;
    text-decoration: none;
    font-weight: 500;
    cursor: pointer;

    &:hover {
        color: $color-secondary;
    }
}

.dashboard-layout {
    display: flex;
    height: 100vh; // Full viewport height
    background: $color-gray-light-2;
    overflow: hidden; // Prevent body scroll

    .sidebar {
        width: 270px;
        background: $color-white;
        border-right: 1px solid $box-border-color;
        display: flex;
        flex-direction: column;
        padding: 0;
        position: fixed;
        height: 100vh;
        overflow-y: auto;
        left: 0;
        top: 0;
        z-index: 1; // Higher z-index for mobile sidebar

        .sidebar-logo {
            font-size: 1.6rem;
            font-weight: bold;
            color: $color-primary;
            padding: 2rem 1.5rem 1rem 1.5rem;
            letter-spacing: -1px;

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        .sidebar-nav {
            display: flex;
            flex-direction: column;
            gap: 0.25rem;
            padding: 0 0.5rem;

            a {
                display: flex;
                align-items: center;
                gap: 1rem;
                padding: 0.75rem 1.5rem;
                color: $color-black;
                border-radius: $border-radius-small;
                font-size: 1.05rem;
                text-decoration: none;
                transition: background 0.15s;
                align-items: flex-end;

                .icon {
                    font-size: 1.2rem;
                }

                &.active,
                &:hover {
                    background: $color-gray-light-2;
                    color: $color-primary;
                }
            }
        }
    }

    .main-area {
        flex: 1;
        display: flex;
        flex-direction: column;
        margin-left: 270px;
        width: calc(100vw - 270px); // Constrain width to prevent overflow
        min-width: 0; // Allow flex item to shrink
        height: 100vh; // Full height
        z-index: 1; // Lower than mobile sidebar

        .topbar {
            display: flex;
            align-items: center;
            justify-content: space-between;
            background: $color-white;
            border-bottom: 1px solid $box-border-color;
            padding: 0.75rem 2rem;
            position: fixed;
            top: 0;
            left: 270px;
            right: 0;
            z-index: 100;
            width: calc(100vw - 270px); // Constrain width
            height: 70px; // Fixed height
            gap: 1rem;

            .search-box {
                display: flex;
                align-items: center;
                background: $color-gray-light-2;
                border-radius: 6px;
                padding: 0.4rem 1rem;
                gap: 0.5rem;

                input {
                    border: none;
                    background: transparent;
                    outline: none;
                    font-size: 1rem;
                    width: 200px;
                }
            }

            .manatal-import-button {
                display: flex;
                align-items: center;
                gap: 0.5rem;
                cursor: pointer;
                position: relative;

                img {
                    width: 120px;
                    height: 100%;
                    object-fit: contain;
                }

                .notification-count {
                    background: $color-danger;
                    color: $color-white;
                    border-radius: 50%;
                    padding: 0.2rem 0.5rem;
                    font-size: 0.8rem;
                    font-weight: 500;
                    position: absolute;
                    top: -10px;
                    right: -10px;
                    width: 22px;
                    height: 22px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                &:hover {
                    .notification-count {
                        transform: scale(1.15);
                    }
                }
            }

            .topbar-actions {
                display: flex;
                align-items: center;
                gap: 1.2rem;
                flex: 1 1 10%;
                justify-content: flex-end;

                .topbar-cruise-line-container {
                    min-width: 270px;
                    max-width: 300px;
                }

                .topbar-icon {
                    font-size: 1.3rem;
                    color: $color-primary;
                    cursor: pointer;
                }

                .topbar-avatar {
                    font-size: 2rem;
                    color: $color-gray-light;
                }

                .topbar-username {
                    font-weight: 500;
                    color: $color-black;
                }
            }
        }

        .main-content {
            flex: 1;
            padding: 1rem;
            background: $color-gray-light-2;
            margin-top: 70px; // Account for fixed topbar
            height: calc(100vh - 70px); // Remaining height after topbar
            overflow-y: auto; // Only this should scroll
            overflow-x: hidden; // Prevent horizontal scroll
            width: 100%; // Ensure full width
            max-width: 100%; // Prevent overflow
        }
    }
}


// Card styles
.card-root {
    flex: 1;
    display: flex;
    flex-direction: column;
    background: $color-white;
    border-radius: $border-radius-large;
    // box-shadow: 0 4px 32px rgba(44, 62, 80, 0.10);
    margin: 0;
    padding: 1rem 2rem;
    border: 1px solid $box-border-color;
}

@keyframes fadeInCard {
    from {
        opacity: 0;
        transform: translateY(40px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

// Onboarding animations
@keyframes fadeInStep {
    from {
        opacity: 0;
        transform: translateY(10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

// File upload styles
.file-upload-root {
    margin-top: 1.2rem;

    .file-upload-label {
        font-weight: 400;
        margin-bottom: 0.5rem;
        display: block;
        color: $label-color;
    }

    .file-upload-box {
        background: $color-gray-light-2;
        border: 2px dashed $color-gray-light-2;
        border-radius: $border-radius-small;
        padding: 1.2rem 1rem;
        text-align: center;
        cursor: pointer;
        transition: border-color 0.2s, box-shadow 0.2s;

        &:hover {
            border-color: $color-primary;
            box-shadow: 0 2px 8px rgba(42, 123, 228, 0.08);
        }

        .file-upload-placeholder {
            color: $label-color;
            font-size: 1.05rem;
            display: flex;
            gap: 10px;
            width: 100%;
            align-items: center;
            justify-content: center;

            .file-upload-camera {
                font-size: 2.2rem;
                margin-bottom: 0.3rem;
                color: $color-primary;
                animation: cameraPulse 1.5s infinite alternate;
            }
        }

        .file-upload-preview {
            color: $color-primary;
            font-weight: 500;
            font-size: 1.1rem;
            display: flex;
            align-items: center;
            gap: 0.5rem;
            justify-content: center;
        }
    }
}

@keyframes cameraPulse {
    from {
        opacity: 0.7;
    }

    to {
        opacity: 1;
    }
}

// Button styles
.btn-root {
    border: none;
    border-radius: 6px;
    padding: 0.6rem 1.5rem;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: background 0.18s, color 0.18s, box-shadow 0.18s;
    box-shadow: 0 2px 8px rgba(42, 123, 228, 0.04);
    white-space: nowrap;

    &.btn-sm {
        font-size: 0.8rem;
        padding: 0.275rem 0.75rem !important;
        min-height: 30px;
    }

    &.btn-primary {
        background: $color-primary;
        color: $color-white;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.6rem 1rem;

        &:hover:not(:disabled) {
            background: $color-primary-hover;
            box-shadow: 0 4px 16px rgba(42, 123, 228, 0.10);
        }
    }

    &.btn-secondary {
        background: $color-gray-light-2;
        color: $color-primary;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.6rem 1rem;

        &:hover:not(:disabled) {
            background: $color-gray-light;
        }
    }

    &.btn-update {
        background: rgba($color-blue, 0.1);
        border: 1px solid $color-gray-light-2;
        color: $color-blue;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.6rem 1rem;

        &:hover:not(:disabled) {
            background: $color-blue;
            color: $color-white;
        }
    }

    &.btn-outline {
        background: rgba($color-primary, 0.1);
        border: 1px solid $color-gray-light-2;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.6rem 1rem;

        &:hover:not(:disabled) {
            background: $color-primary;
            color: $color-white;
            border: 1px solid $color-primary;
        }
    }

    &.btn-outline-primary {
        background: rgba($color-primary, 0.1);
        color: $color-primary;
        border: 1px solid $color-gray-light-2;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.6rem 1rem;

        &:hover:not(:disabled) {
            background: $color-primary;
            color: $color-white;
            border-color: $color-primary;
        }
    }

    &.btn-warning {
        background: rgba($color-warning, 0.08);
        color: $color-warning;
        border: 1px solid $color-gray-light-2;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.6rem 1rem;

        &:hover:not(:disabled) {
            background: $color-warning;
            color: $color-white;
            border-color: $color-warning;
        }
    }

    &.btn-success {
        // background: $color-success-light;
        background: rgba($color-success-dark, 0.1);
        border: 1px solid $color-gray-light-2;
        color: $color-success-dark;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.6rem 1rem;

        &:hover:not(:disabled) {
            background: $color-success-dark;
            color: $color-white;
            border-color: $color-success-dark;
        }
    }

    &.btn-danger {
        // background: $color-danger-light;
        background: rgba($color-danger, 0.1);
        border: 1px solid $color-gray-light-2;
        color: $color-danger;
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.6rem 1rem;

        &:hover:not(:disabled) {
            background: $color-danger;
            color: $color-white;
            border-color: $color-danger;
        }
    }

    &:disabled {
        background: $fields-border-color-disabled;
        color: $fields-border-color-disabled-text;
        cursor: not-allowed;
    }
}

// Input styles
.input-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.2rem;

    .input-label {
        font-size: 1rem;
        color: $label-color;
        font-weight: 400;
        margin-bottom: 0.35rem;
        transition: color 0.18s;
        letter-spacing: 0.01em;
    }

    .input-text {
        font-size: 1.05rem;
        padding: 0 1rem;
        height: $fields-height;
        border: 1.5px solid $fields-border-color;
        border-radius: $fields-border-radius !important;
        background: $color-white;
        color: $color-black;
        outline: none;
        transition: border 0.18s, box-shadow 0.18s, background 0.18s;
        box-shadow: none;

        &:focus {
            border-color: $fields-border-color-focus;
            background: $color-white;
            box-shadow: 0 2px 8px rgba(42, 123, 228, 0.08);
        }

        &:hover {
            border-color: $fields-border-color-hover;
        }

        &::placeholder {
            color: $color-gray-light;
            opacity: 1;
        }

        &.input-sm {
            height: calc($fields-height - 12px);
            border-radius: $border-radius-xsmall !important;
            font-size: 0.9rem;
            padding: 0 0.5rem;
        }

        &.mw-160 {
            max-width: 150px;
        }
    }

    // For react-select
    .select2__control {
        border-radius: $fields-border-radius !important;
        border: 1.5px solid $fields-border-color;
        background: $color-white;
        min-height: calc($fields-height + 2px);
        box-shadow: none;
        transition: border 0.18s, box-shadow 0.18s;
        font-size: 1.05rem;

        &:hover {
            border-color: $fields-border-color-hover;
        }

        &.select2__control--is-focused {
            border-color: $fields-border-color-focus;
            background: $color-white;
            box-shadow: 0 2px 8px rgba(42, 123, 228, 0.08);
        }
    }

    .select-input-wrapper {
        position: relative;
        display: flex;
        align-items: center;

        >div {
            width: 100%;
        }

        .select-input-icon {
            position: absolute;
            left: 12px;
            z-index: 2;
            color: $color-gray;
            font-size: 0.9rem;
            pointer-events: none;
        }

        .select2__control {
            padding-left: 1rem;
        }

        .select2__value-container {
            padding-left: 0;
        }
    }

    .select2__value-container {
        padding: 0 1rem;
    }

    .select2__single-value {
        color: $color-black;
        font-size: 1.05rem;
        line-height: $fields-height;
    }

    .select2__placeholder {
        color: $color-gray-light;
        font-size: 1.05rem;
        line-height: $fields-height;
    }

    .select2__menu {
        border-radius: $border-radius-small;
        font-size: 1.05rem;
        z-index: 3;

        .select2__menu-list {
            div[id^="react-select-"] {
                cursor: pointer;

                &:hover {
                    background: $color-gray-light-2 !important;
                }

                div:last-child {
                    color: $color-primary !important;
                }
            }
        }
    }

    .select2__indicator-separator {
        display: none;
    }

    .select2__dropdown-indicator {
        color: $color-gray-light;
        transition: color 0.18s;

        &:hover {
            color: $color-primary;
        }
    }

    .input-date {
        font-size: 1.05rem;
        padding: 0 1rem;
        min-width: 180px;
        height: $fields-height;
        border: 1.5px solid $fields-border-color;
        border-radius: $fields-border-radius !important;
        font-family: $font-family-primary;

        &:focus {
            border-color: $fields-border-color-focus;
            background: $color-white;
            box-shadow: 0 2px 8px rgba(42, 123, 228, 0.08);
            outline: none;
        }

        &:hover {
            border-color: $fields-border-color-hover;
        }

        &::placeholder {
            color: $color-gray-light;
            opacity: 1;
        }
    }

    input[type="date"]::placeholder {
        color: $color-gray-light !important;
    }

    input[type="date"]::-webkit-input-placeholder {
        color: $color-gray-light !important;
    }

    input[type="date"]::-moz-placeholder {
        color: $color-gray-light !important;
        opacity: 1;
    }

    .input-date::-webkit-calendar-picker-indicator {
        filter: grayscale(1) brightness(1.7);
        opacity: 0.4;
        cursor: pointer;
    }
}

// Animate label color on focus
.input-group:focus-within .input-label {
    color: $color-primary;
}

// Radio group styles
.radio-group {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 0.5rem;

    .radio-option {
        display: flex;
        align-items: flex-start;
        gap: 1rem;
        padding: 1.2rem;
        border: 2px solid $fields-border-color;
        border-radius: $border-radius-small;
        cursor: pointer;
        transition: all 0.2s;
        background: $color-white;

        &:hover {
            border-color: $color-primary;
            background: $color-gray-light-2;

            .radio-custom {
                border: 1px solid $color-primary;
            }
        }

        input[type="radio"] {
            margin: 0;
            margin-top: 0.2rem;
            display: none;
        }

        .radio-custom {
            width: 18px;
            height: 18px;
            border: 2px solid $fields-border-color;
            border-radius: 50%;
            margin-top: 0.2rem;
            position: relative;
            transition: all 0.2s;

            &:after {
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 12px;
                height: 12px;
                border-radius: 50%;
                background: $color-primary;
                opacity: 0;
                transition: opacity 0.2s;
                border: 3px solid $color-white;
            }
        }

        input[type="radio"]:checked+.radio-custom {
            border-color: $color-primary;
            background: $color-primary;

            &:after {
                opacity: 1;
            }
        }

        .radio-content {
            flex: 1;

            strong {
                display: block;
                color: $color-black;
                font-size: 1.05rem;
                font-weight: 600;
                margin-bottom: 0.2rem;
            }

            small {
                color: $color-gray;
                font-size: 0.9rem;
            }
        }
    }
}

// Checkbox group styles
.checkbox-group {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 0.5rem;

    .checkbox-option {
        display: flex;
        align-items: flex-start;
        gap: 1rem;
        padding: 1.2rem;
        border: 2px solid $fields-border-color;
        border-radius: $border-radius-small;
        cursor: pointer;
        transition: all 0.2s;
        background: $color-white;

        &:hover {
            border-color: $color-primary;
            background: $color-gray-light-2;

            .checkbox-custom {
                border: 1px solid $color-primary;
            }
        }

        &.disabled {
            cursor: not-allowed;
            opacity: 0.6;
            border-color: $color-gray-light;

            &:hover {
                border-color: $color-warning;
                background: $color-warning-light;

                .checkbox-custom {
                    border: 2px solid $color-gray-light;
                }

                .checkbox-content {
                    strong {
                        color: $color-danger;
                    }

                    small {
                        color: $color-danger;
                    }
                }
            }

            .checkbox-custom {
                border-color: $color-gray-light;
                background: $color-gray-light-2;
            }

            .checkbox-content {
                strong {
                    color: $color-gray;
                }

                small {
                    color: $color-gray;
                }
            }
        }

        input[type="checkbox"] {
            margin: 0;
            margin-top: 0.2rem;
            display: none;

            &:disabled {
                cursor: not-allowed;
            }
        }

        .checkbox-custom {
            width: 18px;
            height: 18px;
            border: 2px solid $fields-border-color;
            border-radius: $border-radius-xsmall;
            margin-top: 0.2rem;
            position: relative;
            transition: all 0.2s;
            background: $color-white;

            &:after {
                content: '✓';
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                color: $color-white;
                font-size: 12px;
                font-weight: bold;
                opacity: 0;
                transition: opacity 0.2s;
            }
        }

        input[type="checkbox"]:checked+.checkbox-custom {
            border-color: $color-primary;
            background: $color-primary;

            &:after {
                opacity: 1;
            }
        }

        // Green styling when enabled and checked (all steps complete)
        &:not(.disabled) {
            input[type="checkbox"]:checked+.checkbox-custom {
                border-color: $color-success;
                background: $color-success;
            }

            &:hover {
                border-color: $color-success;
                background: rgba($color-success, 0.05);

                .checkbox-custom {
                    border: 1px solid $color-success;
                }
            }

            &.checked {
                border-color: $color-success;
                background: rgba($color-success, 0.1);

                .checkbox-content {
                    strong {
                        // color: $color-success;
                    }

                    small {
                        // color: $color-success;
                    }
                }
            }
        }

        .checkbox-content {
            flex: 1;

            strong {
                display: block;
                color: $color-black;
                font-size: 1.05rem;
                font-weight: 600;
                margin-bottom: 0.2rem;
            }

            small {
                color: $color-gray;
                font-size: 0.9rem;
            }
        }
    }
}

// Responsive styles
@media (max-width: 900px) {
    .crew-profile-onboarding-layout {
        flex-direction: column;
        align-items: stretch;
    }

    .crew-profile-onboarding-main {
        margin-top: 0;
        padding: 0 0.5rem;
    }
}

// Notification dropdown styles
.notification-dropdown {
    position: relative;
    display: inline-block;

    .notification-trigger {
        position: relative;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        transition: background 0.2s;

        &:hover {
            background: $color-gray-light-2;
        }

        .notification-badge {
            position: absolute;
            top: -2px;
            right: -2px;
            background: $color-danger;
            color: $color-white;
            border-radius: 50%;
            width: 23px;
            height: 23px;
            font-size: 0.7rem;
            font-weight: 600;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 2px solid $color-white;
        }
    }

    .notification-panel {
        position: absolute;
        top: 100%;
        right: 0;
        width: 380px;
        max-height: 500px;
        background: $color-white;
        border: 1px solid $box-border-color;
        border-radius: $border-radius;
        box-shadow: 0 8px 32px rgba(0, 0, 0, 0.12);
        z-index: 1000;
        margin-top: 0.5rem;
        overflow: hidden;

        .notification-header {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 1rem 1.2rem;
            border-bottom: 1px solid $box-border-color;
            background: $color-gray-light-3;

            h4 {
                margin: 0;
                color: $color-black;
                font-size: 1.1rem;
                font-weight: 600;
            }

            .notification-close {
                background: none;
                border: none;
                color: $color-gray;
                cursor: pointer;
                padding: 0.3rem;
                border-radius: $border-radius-xsmall;
                transition: all 0.2s;

                &:hover {
                    background: $color-gray-light-2;
                    color: $color-black;
                }
            }
        }

        .notification-list {
            max-height: 350px;
            overflow-y: auto;

            .notification-item {
                display: flex;
                gap: 0.8rem;
                padding: 1rem 1.2rem;
                border-bottom: 1px solid $box-border-color;
                cursor: pointer;
                transition: background 0.2s;

                &:hover {
                    background: $color-gray-light-3;
                }

                &:last-child {
                    border-bottom: none;
                }

                &.unread {
                    background: rgba(1, 96, 113, 0.05);
                    border-left: 3px solid $color-primary;

                    &:hover {
                        background: rgba(1, 96, 113, 0.08);
                    }
                }

                .notification-icon-wrapper {
