// Process Overlay
.process-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    animation: fadeIn 0.3s ease-out;

    .process-overlay-content {
        background: $color-white;
        border-radius: $border-radius-large;
        padding: 2rem;
        width: 90%;
        max-width: 800px;
        position: relative;
        box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);

        .close-button {
            position: absolute;
            top: 1rem;
            right: 1rem;
            background: none;
            border: none;
            color: $color-gray;
            cursor: pointer;
            padding: 0.5rem;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: color 0.2s;

            &:hover {
                color: $color-danger;
            }

            svg {
                font-size: 1.25rem;
            }
        }

        .process-steps {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin: 2rem 0;
            padding: 0 1rem;
        }

        .process-step {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            flex: 1.5 1;
            position: relative;
            opacity: 0.5;
            transition: all 0.3s ease-out;

            &.completed {
                opacity: 1;

                .step-icon {
                    background-color: $color-success;
                    border-color: $color-success;
                    color: $color-white;
                }
            }

            &.current {
                opacity: 1;

                .step-icon {
                    background-color: $color-primary;
                    border-color: $color-primary;
                    color: $color-white;
                }
            }

            .step-icon {
                width: 48px;
                height: 48px;
                border-radius: 50%;
                background: $color-white;
                border: 2px solid $color-gray-light-2;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-bottom: 1rem;
                transition: all 0.3s ease-out;

                svg {
                    font-size: 1.5rem;
                }

                .spinner-icon {
                    animation: spin 1s linear infinite;
                }
            }

            .step-content {
                h4 {
                    font-size: 1rem;
                    margin: 0 0 0.5rem;
                    color: $color-primary;
                }

                p {
                    font-size: 0.875rem;
                    color: $color-gray;
                    margin: 0;
                    max-width: 200px;
                    height: 42px;
                }
            }
        }

        .step-arrow {
            flex: 0.5;
            height: 2px;
            background-color: $color-gray-light-2;
            position: relative;
            margin-top: -78px;
            transition: background-color 0.3s ease-out;

            &.completed {
                background-color: $color-success;
            }

            &::after {
                content: '';
                position: absolute;
                right: 0;
                top: 50%;
                transform: translate(50%, -50%) rotate(45deg);
                width: 8px;
                height: 8px;
                border-top: 2px solid $color-gray-light-2;
                border-right: 2px solid $color-gray-light-2;
            }

            &.completed::after {
                border-color: $color-success;
            }
        }

        .progress-bar {
            height: 6px;
            background-color: $color-gray-light-2;
            border-radius: $border-radius-xxsmall;
            margin: 2rem 0 1rem;
            overflow: hidden;

            .progress-fill {
                height: 100%;
                background-color: $color-success;
                transition: width 0.3s ease-out;
            }
        }

        .progress-text {
            text-align: center;
            font-size: 0.875rem;
            color: $color-gray;
        }
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

