// Review step improvements
.onboarding-review {
    .review-summary {
        .review-panel {
            border: 2px solid $color-gray-light;
            border-radius: $border-radius;
            transition: all 0.3s ease;

            // Status-based styling
            &.status-complete {
                border-color: $color-success;
                background: rgba(74, 217, 145, 0.02);

                .review-panel-header {
                    background: rgba(74, 217, 145, 0.05);
                }
            }

            &.status-partial {
                border-color: $color-warning;
                background: rgba(243, 156, 18, 0.02);

                .review-panel-header {
                    background: rgba(243, 156, 18, 0.05);
                }
            }

            &.status-missing {
                border-color: $color-warning;
                background: rgba(254, 233, 205, 0.1);

                .review-panel-header {
                    background: rgba(254, 233, 205, 0.3);
                }
            }

            .review-panel-header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0.8rem;
                border-bottom: 1px solid $color-gray-light;
                border-radius: $border-radius $border-radius 0 0;

                .review-panel-icon-wrapper {
                    display: flex;
                    align-items: center;
                    gap: 1rem;
                    position: relative;

                    .review-panel-icon {
                        font-size: 1.5rem;
                        color: $color-primary;
                        margin-right: 0.5rem;
                    }

                    .status-icon {
                        position: absolute;
                        top: -5px;
                        right: -5px;
                        width: 24px;
                        height: 24px;
                        border-radius: 50%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 0.8rem;
                        border: 2px solid $color-white;

                        &.complete {
                            background: $color-success;
                            color: $color-white;
                        }

                        &.partial {
                            background: $color-warning;
                            color: $color-white;
                        }

                        &.missing {
                            background: $color-gray;
                            color: $color-white;
                        }
                    }
                }

                .review-panel-title {
                    display: flex;
                    align-items: center;
                    gap: 1rem;
                    flex: 1;
                    justify-content: space-between;

                    h4 {
                        margin: 0;
                        color: $color-black;
                        font-weight: 600;
                        font-size: 1.2rem;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        max-width: 80%;
                    }

                    .status-badge {
                        padding: 0.3rem 0.8rem;
                        border-radius: $border-radius-xlarge;
                        font-size: 0.75rem;
                        font-weight: 600;
                        text-transform: uppercase;
                        letter-spacing: 0.5px;

                        &.status-complete {
                            background: rgba(74, 217, 145, 0.1);
                            color: $color-success;
                        }

                        &.status-partial {
                            background: rgba(243, 156, 18, 0.1);
                            color: $color-warning;
                        }

                        &.status-missing {
                            background: rgba(192, 142, 28, 0.1);
                            color: $color-gray;
                        }
                    }
                }
            }

            .review-panel-content {
                padding: 0.2rem 1.5rem;

                .review-item {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding: 0.8rem 0;
                    border-bottom: 1px solid $color-gray-light-3;

                    &:last-child {
                        border-bottom: none;
                    }

                    .review-label {
                        font-weight: 600;
                        color: $color-gray;
                        font-size: 0.9rem;
                    }

                    .review-value {
                        color: $color-black;
                        font-weight: 500;
                        font-size: 0.9rem;
                        text-align: right;
                        max-width: 60%;
                        word-break: break-word;
                    }
                }
            }
        }
    }
}