@import '../variables';

// Notification detail modal styles
.notification-detail-modal {
    .modal-body {
        padding: 0;
    }

    .notification-detail-content {
        display: flex;
        gap: 1.5rem;
        align-items: flex-start;

        .notification-detail-icon {
            flex-shrink: 0;
            margin-top: 0.5rem;

            .notification-icon {
                font-size: 2.5rem;

                &.warning {
                    color: $color-warning;
                }

                &.info {
                    color: $color-primary;
                }

                &.success {
                    color: $color-success;
                }

                &.danger {
                    color: $color-danger;
                }
            }
        }

        .notification-detail-info {
            flex: 1;

            h2 {
                margin: 0 0 1rem 0;
                color: $color-black;
                font-size: 1.4rem;
                font-weight: 600;
                line-height: 1.3;
            }

            .notification-detail-meta {
                display: flex;
                gap: 1rem;
                margin-bottom: 1.5rem;
                align-items: center;
                flex-wrap: wrap;

                .notification-detail-time {
                    color: $color-gray;
                    font-size: 0.9rem;
                }

                .notification-detail-status {
                    padding: 0.3rem 0.8rem;
                    border-radius: $border-radius-xlarge;
                    font-size: 0.8rem;
                    font-weight: 500;

                    &.unread {
                        background: rgba(1, 96, 113, 0.1);
                        color: $color-primary;
                    }

                    &.read {
                        background: $color-gray-light-2;
                        color: $color-gray;
                    }
                }

                .notification-detail-category {
                    padding: 0.3rem 0.8rem;
                    border-radius: $border-radius-xlarge;
                    font-size: 0.8rem;
                    font-weight: 500;
                    background: $color-gray-light-2;
                    color: $color-gray;
                }

                .notification-detail-bulk {
                    padding: 0.3rem 0.8rem;
                    border-radius: $border-radius-xlarge;
                    font-size: 0.8rem;
                    font-weight: 500;
                    background: rgba(1, 96, 113, 0.1);
                    color: $color-primary;
                }
            }

            .notification-detail-message {
                color: $color-black;
                font-size: 1.1rem;
                line-height: 1.6;
                margin-bottom: 1.5rem;
                background: $color-gray-light-3;
                padding: 1.5rem;
                border-radius: $border-radius;
                border-left: 4px solid $color-primary;
            }

            .notification-detail-action-button {
                margin-top: 1.5rem;
                padding-top: 1.5rem;
                border-top: 1px solid $box-border-color;

                .btn-root {
                    display: flex;
                    align-items: center;
                    gap: 0.5rem;
                    padding: 0.8rem 1.5rem;
                    font-size: 1rem;
                    font-weight: 500;
                    min-width: 200px;
                    justify-content: center;

                    &.btn-warning {
                        background: $color-warning;
                        color: $color-white;

                        &:hover:not(:disabled) {
                            background: $color-warning-hover;
                            box-shadow: 0 4px 16px rgba(243, 156, 18, 0.15);
                        }
                    }

                    &.btn-info {
                        background: $color-primary;
                        color: $color-white;

                        &:hover:not(:disabled) {
                            background: $color-primary-hover;
                            box-shadow: 0 4px 16px rgba(1, 96, 113, 0.15);
                        }
                    }

                    &.btn-success {
                        background: $color-success;
                        color: $color-white;
                        border: 1px solid $color-success;

                        &:hover:not(:disabled) {
                            background: darken($color-success, 10%);
                            color: $color-white;
                            border-color: darken($color-success, 10%);
                        }
                    }

                    &.btn-danger {
                        color: $color-danger;
                        border-color: $color-danger;

                        &:hover:not(:disabled) {
                            background: $color-danger;
                            color: $color-white;
                            border-color: $color-danger;
                        }
                    }
                }
            }
        }
    }
}

// Templates Modal Styles
.templates-modal {
    .modal-body {
        padding: 0;
    }

    .templates-content {

        .templates-header {
            margin-bottom: 1rem;
            text-align: center;

            h6 {
                font-size: 1.3rem;
                font-weight: 600;
                color: $color-black;
                margin-bottom: 0.5rem;
            }

            p {
                color: $color-gray;
                font-size: 0.95rem;
                margin: 0;
            }
        }

        .templates-grid {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
            gap: 1.5rem;
            max-height: 60vh;
            overflow-y: auto;
            padding: 0.15rem 0;

            .template-card {
                background: $color-white;
                border: 1px solid $box-border-color;
                border-radius: $border-radius;
                padding: 1.5rem;
                transition: all 0.2s;
                cursor: pointer;

                &:hover {
                    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
                    transform: translateY(-2px);
                    border-color: $color-primary;
                }

                .template-card-header {
                    display: flex;
                    align-items: flex-start;
                    gap: 1rem;
                    margin-bottom: 0.5rem;

                    .template-icon {
                        flex-shrink: 0;
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        font-size: 1.2rem;

                        &.warning {
                            background: rgba(243, 156, 18, 0.1);
                            color: $color-warning;
                        }

                        &.success {
                            background: rgba(74, 217, 145, 0.1);
                            color: $color-success;
                        }

                        &.info {
                            background: rgba(1, 96, 113, 0.1);
                            color: $color-primary;
                        }

                        &.danger {
                            background: rgba(231, 76, 60, 0.1);
                            color: $color-danger;
                        }
                    }

                    .template-title {
                        flex: 1;

                        h6 {
                            font-size: 1.1rem;
                            font-weight: 600;
                            color: $color-black;
                            margin: 0 0 0.5rem 0;
                            line-height: 1.3;
                        }

                        .template-category {
                            display: inline-block;
                            padding: 0.3rem 0.8rem;
                            border-radius: $border-radius-xlarge;
                            font-size: 0.8rem;
                            font-weight: 500;
                            background: $color-gray-light-2;
                            color: $color-gray;
                            text-transform: capitalize;
                        }
                    }
                }

                .template-card-content {
                    .template-field {
                        margin-bottom: 1rem;



                        &:last-child {
                            margin-bottom: 0;
                        }

                        label {
                            display: block;
                            font-size: 0.75rem;
                            font-weight: 600;
                            color: $color-gray;
                            margin-bottom: 0.3rem;
                            text-transform: uppercase;
                            letter-spacing: 0.5px;
                        }

                        p {
                            margin: 0;
                            color: $color-black;
                            font-size: 0.75rem;
                            line-height: 1.4;
                            background: $color-gray-light-3;
                            padding: 0.5rem;
                            border-radius: $border-radius-small;
                            border-left: 3px solid $color-primary;
                        }

                        &.multi-line {
                            p {
                                min-height: 68px;
                            }
                        }

                        .template-type {
                            display: inline-block;
                            padding: 0.3rem 0.8rem;
                            border-radius: $border-radius-xlarge;
                            font-size: 0.8rem;
                            font-weight: 500;
                            text-transform: capitalize;

                            &.warning {
                                background: rgba(243, 156, 18, 0.1);
                                color: $color-warning;
                            }

                            &.success {
                                background: rgba(74, 217, 145, 0.1);
                                color: $color-success;
                            }

                            &.info {
                                background: rgba(1, 96, 113, 0.1);
                                color: $color-primary;
                            }

                            &.danger {
                                background: rgba(231, 76, 60, 0.1);
                                color: $color-danger;
                            }
                        }

                        .template-variables {
                            display: flex;
                            flex-wrap: wrap;
                            gap: 0.5rem;

                            .variable-tag {
                                display: inline-block;
                                padding: 0.3rem 0.6rem;
                                background: rgba(1, 96, 113, 0.1);
                                color: $color-primary;
                                border-radius: $border-radius-small;
                                font-size: 0.8rem;
                                font-weight: 500;
                                border: 1px solid rgba(1, 96, 113, 0.2);
                            }
                        }
                    }
                }

                .template-card-actions {
                    margin-top: 1.5rem;
                    padding-top: 1.5rem;
                    border-top: 1px solid $box-border-color;

                    .btn-root {
                        width: 100%;
                        justify-content: center;
                        gap: 0.5rem;
                    }
                }
            }
        }
    }
}

