// Notifications page styles
.notifications-page {
    .notifications-filters {
        display: flex;
        justify-content: flex-end;
    }

    .advanced-notification-controls {
        display: flex;
        gap: 1rem;
    }

    .notifications-list {
        .notification-card {
            border: 1px solid $box-border-color;
            border-radius: $border-radius;
            margin-bottom: 1rem;
            transition: all 0.2s;
            cursor: context-menu;

            &:hover {
                box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
                transform: translateY(-1px);
            }

            &.unread {
                border-left: 4px solid $color-primary;
                background: rgba(1, 96, 113, 0.02);
            }

            .notification-card-header {
                display: flex;
                gap: 1rem;
                padding: 1.2rem;

                .notification-card-icon {
                    flex-shrink: 0;
                    margin-top: 0.2rem;

                    .notification-icon {
                        font-size: 1.3rem;

                        &.warning {
                            color: $color-warning;
                        }

                        &.info {
                            color: $color-primary;
                        }

                        &.success {
                            color: $color-success;
                        }
                    }
                }

                .notification-card-content {
                    flex: 1;
                    min-width: 0;

                    .notification-card-title {
                        font-weight: 600;
                        color: $color-black;
                        font-size: 1rem;
                        margin-bottom: 0.4rem;
                        line-height: 1.3;
                    }

                    .notification-card-message {
                        color: $color-gray;
                        font-size: 0.9rem;
                        line-height: 1.4;
                        margin-bottom: 0.4rem;
                    }

                    .notification-card-time {
                        color: $color-gray-light;
                        font-size: 0.8rem;
                    }
                }

                .notification-card-actions {
                    display: flex;
                    gap: 0.5rem;
                    flex-shrink: 0;
                    align-items: center;

                    .notification-action-btn {
                        background: none;
                        border: none;
                        padding: 0.4rem;
                        border-radius: 6px;
                        cursor: pointer;
                        transition: all 0.2s;
                        font-size: 0.8rem;
                        width: 40px;
                        height: 40px;

                        &.read-btn {
                            color: $color-success;

                            &:hover {
                                background: rgba(74, 217, 145, 0.1);
                            }
                        }

                        &.delete-btn {
                            color: $color-danger;

                            &:hover {
                                background: rgba(231, 76, 60, 0.1);
                            }
                        }
                    }
                }
            }
        }
    }

    .notifications-empty {
        text-align: center;
        padding: 3rem 1rem;
        color: $color-gray;

        .notifications-empty-icon {
            font-size: 3rem;
            margin-bottom: 1rem;
            opacity: 0.5;
        }

        h3 {
            margin: 0 0 0.5rem 0;
            color: $color-black;
            font-size: 1.3rem;
            font-weight: 600;
        }

        p {
            margin: 0;
            font-size: 0.95rem;
        }
    }

}

