@import '../variables.scss';

.cruise-line-configuration-page {
    .configuration-content {
        padding: 20px;

        .cruise-lines-section {
            h3 {
                margin-bottom: 20px;
                color: $color-primary;
                font-size: 1.5rem;
                font-weight: 600;
            }

            .cruise-lines-grid {
                display: flex;
                flex-direction: column;
                gap: 20px;

                .cruise-line-card {
                    border-radius: 12px;
                    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
                    border: 1px solid $color-gray-light;
                    overflow: hidden;


                    .cruise-line-header {
                        display: flex;
                        background: $color-gray-light-4;
                        justify-content: space-between;
                        align-items: flex-start;
                        padding-bottom: 15px;
                        border-bottom: 1px solid $color-gray-light;
                        padding: 20px;

                        .cruise-line-info {
                            display: flex;
                            align-items: flex-start;
                            gap: 15px;
                            flex: 1;

                            .cruise-line-icon {
                                width: 50px;
                                height: 50px;
                                background: $color-primary;
                                border-radius: 10px;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                color: $color-white;
                                font-size: 1.5rem;
                                flex-shrink: 0;
                            }

                            .cruise-line-details {
                                flex: 1;

                                h4 {
                                    margin: 0;
                                    color: $color-primary;
                                    font-size: 1.4rem;
                                    font-weight: 600;
                                }

                                .entity {
                                    margin: 0 0 8px 0;
                                    color: $color-secondary;
                                    font-size: 0.95rem;
                                    font-weight: 500;
                                }

                                .description {
                                    margin: 0;
                                    color: $color-gray;
                                    font-size: 0.9rem;
                                    line-height: 1.4;
                                }

                                .cruise-line-meta {
                                    display: flex;
                                    gap: 15px;

                                    .meta-item {
                                        display: flex;
                                        align-items: center;
                                        gap: 5px;
                                        color: $color-gray;
                                        font-size: 0.85rem;
                                        font-weight: 500;

                                        svg {
                                            color: $color-primary;
                                        }
                                    }
                                }
                            }
                        }

                        .cruise-line-actions {
                            display: flex;
                            gap: 8px;

                            .btn {
                                padding: 8px 12px;
                                font-size: 0.85rem;
                            }
                        }
                    }

                    .vessels-section {
                        padding: 15px 20px;

                        .vessels-header {
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                            margin-bottom: 15px;

                            h5 {
                                margin: 0;
                                color: $color-black;
                                font-size: 1.2rem;
                                font-weight: 600;
                            }

                            .btn {
                                padding: 8px 12px;
                                font-size: 0.85rem;
                            }
                        }

                        .vessels-list {
                            .vessel-card {
                                background: $color-gray-light-5;
                                border-radius: 10px;
                                padding: 15px;
                                margin-bottom: 15px;
                                border: 1px solid $color-gray-light;

                                .vessel-header {
                                    display: flex;
                                    justify-content: space-between;
                                    align-items: flex-start;
                                    margin-bottom: 15px;
                                    border-bottom: 1px solid $color-gray-light;
                                    padding-bottom: 10px;

                                    .vessel-info {
                                        display: flex;
                                        align-items: flex-start;
                                        gap: 12px;
                                        flex: 1;

                                        .vessel-icon {
                                            width: 40px;
                                            height: 40px;
                                            background: $color-secondary;
                                            border-radius: 8px;
                                            display: flex;
                                            align-items: center;
                                            justify-content: center;
                                            color: $color-white;
                                            font-size: 1.2rem;
                                            flex-shrink: 0;
                                        }

                                        .vessel-details {
                                            flex: 1;

                                            h6 {
                                                margin: 0 0 2px 0;
                                                color: $color-black;
                                                font-size: 1.1rem;
                                                font-weight: 600;
                                            }

                                            .vessel-meta {
                                                display: flex;
                                                gap: 12px;

                                                .meta-item {
                                                    display: flex;
                                                    align-items: center;
                                                    gap: 4px;
                                                    color: $color-gray;
                                                    font-size: 0.8rem;
                                                    font-weight: 500;

                                                    svg {
                                                        color: $color-secondary;
                                                    }
                                                }
                                            }
                                        }
                                    }

                                    .vessel-actions {
                                        display: flex;
                                        gap: 6px;

                                        .btn {
                                            padding: 6px 10px;
                                            font-size: 0.8rem;
                                        }
                                    }
                                }

                                .vessel-content {
                                    display: grid;
                                    grid-template-columns: 1fr 1fr;
                                    gap: 20px;

                                    .ports-section,
                                    .positions-section {
                                        padding: 1rem;
                                        background-color: $color-gray-light-3;
                                        border-radius: 10px;
                                        border: 1px solid rgba($color-primary, 0.5);

                                        h6 {
                                            color: $color-primary;
                                            font-size: 1rem;
                                            font-weight: 600;
                                            margin: 0;
                                            padding-top: 0.6rem;
                                        }

                                        .ports-header,
                                        .positions-header {
                                            display: flex;
                                            justify-content: space-between;
                                            align-items: center;
                                            margin-bottom: 10px;
                                            padding-bottom: 10px;
                                            border-bottom: 1px solid rgba($color-primary, 0.5);

                                            .btn {
                                                padding: 4px 8px;
                                                font-size: 0.75rem;
                                            }
                                        }

                                        .ports-list {
                                            .port-item {
                                                display: flex;
                                                justify-content: space-between;
                                                align-items: center;
                                                gap: 8px;
                                                padding: 8px 8px 8px 12px;
                                                background: $color-white;
                                                border-radius: 6px;
                                                margin-bottom: 6px;
                                                border: 1px solid $color-gray-light;

                                                .port-info {
                                                    display: flex;
                                                    align-items: center;
                                                    gap: 8px;
                                                    flex: 1;

                                                    svg {
                                                        color: $color-secondary;
                                                        font-size: 0.9rem;
                                                    }

                                                    span {
                                                        flex: 1;
                                                        font-size: 0.9rem;
                                                        font-weight: 500;
                                                        color: $color-black;
                                                    }

                                                    small {
                                                        color: $color-gray;
                                                        font-size: 0.8rem;
                                                    }
                                                }

                                                .port-actions {
                                                    display: flex;
                                                    gap: 4px;

                                                    .btn-root {
                                                        padding: 0.2rem 0.6rem !important;
                                                    }
                                                }
                                            }

                                            .port-item-empty {
                                                padding: 4px 8px;
                                                font-size: 0.9rem;
                                                font-weight: 500;
                                            }
                                        }

                                        .positions-list {
                                            .position-item {
                                                display: flex;
                                                justify-content: space-between;
                                                align-items: center;
                                                padding: 8px 8px 8px 10px;
                                                border-radius: 6px;
                                                margin-bottom: 6px;
                                                border: 1px dashed transparent;
                                                transition: all 0.2s ease;

                                                .position-info {
                                                    display: flex;
                                                    align-items: center;
                                                    gap: 8px;

                                                    .position-icon {
                                                        font-size: 1.1rem;
                                                    }

                                                    .position-label {
                                                        font-size: 0.9rem;
                                                        font-weight: 500;
                                                    }
                                                }

                                                .position-actions {
                                                    display: flex;
                                                    gap: 4px;

                                                    .btn-root {
                                                        padding: 0.2rem 0.6rem !important;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }

                            .vessel-item-empty {
                                border-top: 1px solid $color-gray-light;
                                padding: 8px;
                                font-size: 0.9rem;
                                font-weight: 500;
                            }
                        }
                    }
                }
            }
        }
    }

    .color-picker {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;

        .color-option {
            width: 30px;
            height: 30px;
            border-radius: 50%;
            border: 2px solid transparent;
            cursor: pointer;
            transition: all 0.2s ease;

            &.selected {
                border-color: $color-black;
                transform: scale(1.1);
            }

            &:hover {
                transform: scale(1.05);
            }
        }
    }

    .icon-picker {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;

        .icon-option {
            width: 40px;
            height: 40px;
            border: 2px solid $color-gray-light;
            border-radius: 8px;
            background: $color-white;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 1.2rem;
            transition: all 0.2s ease;

            &.selected {
                border-color: $color-primary;
                background: $color-primary;
                color: $color-white;
            }

            &:hover {
                border-color: $color-primary;
            }
        }
    }
}

// Responsive adjustments
@media (max-width: 768px) {
    .cruise-line-configuration-page {
        .configuration-content {
            padding: 15px;

            .cruise-lines-section {
                .cruise-lines-grid {
                    grid-template-columns: 1fr;
                    gap: 15px;

                    .cruise-line-card {
                        padding: 15px;

                        .cruise-line-header {
                            flex-direction: column;
                            gap: 15px;

                            .cruise-line-info {
                                flex-direction: column;
                                gap: 10px;

                                .cruise-line-icon {
                                    width: 40px;
                                    height: 40px;
                                    font-size: 1.2rem;
                                }
                            }

                            .cruise-line-actions {
                                align-self: flex-end;
                            }
                        }

                        .vessels-section {
                            .vessels-list {
                                .vessel-card {
                                    .vessel-header {
                                        flex-direction: column;
                                        gap: 10px;

                                        .vessel-actions {
                                            align-self: flex-end;
                                        }
                                    }

                                    .vessel-content {
                                        grid-template-columns: 1fr;
                                        gap: 15px;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        .modal-overlay {
            .modal-content {
                width: 95%;
                margin: 20px;

                .modal-header,
                .modal-body,
                .modal-footer {
                    padding-left: 15px;
                    padding-right: 15px;
                }
            }
        }
    }
}