@import '../variables';

.login-page {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);

    .login-background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url('https://www.effyjewelry.com/cdn/shop/files/06222025_Summer3_Homepage_07.jpg?v=1750347081&width=2000');
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        opacity: 0.3;
        z-index: 1;

        .background-overlay {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(135deg, rgba(102, 126, 234, 0.8) 0%, rgba(118, 75, 162, 0.8) 100%);
            z-index: 2;
            opacity: 0.1;
        }

        .floating-elements {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 3;
            pointer-events: none;

            .floating-ship,
            .floating-anchor,
            .floating-globe {
                position: absolute;
                color: rgba(255, 255, 255, 0.1);
                font-size: 3rem;
                animation: float 6s ease-in-out infinite;

                &:nth-child(1) {
                    top: 20%;
                    left: 10%;
                    animation-delay: 0s;
                }

                &:nth-child(2) {
                    top: 60%;
                    right: 15%;
                    animation-delay: 2s;
                }

                &:nth-child(3) {
                    bottom: 30%;
                    left: 20%;
                    animation-delay: 4s;
                }
            }
        }
    }

    .login-container {
        position: relative;
        z-index: 10;
        width: 100%;
        max-width: 50vw;
        padding: 2rem;

        .login-card {
            background: rgba(255, 255, 255, 0.95);
            backdrop-filter: blur(20px);
            border-radius: 20px;
            box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
            padding: 3rem;
            border: 1px solid rgba(255, 255, 255, 0.2);

            .login-header {
                text-align: center;
                margin-bottom: 2.5rem;

                .logo-section {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 1rem;
                    margin-bottom: 1rem;

                    .logo-icon {
                        width: 60px;
                        height: 60px;
                        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
                        border-radius: 15px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        color: white;
                        font-size: 1.8rem;
                        box-shadow: 0 8px 16px rgba(102, 126, 234, 0.3);
                    }

                    .logo-text {
                        h1 {
                            margin: 0;
                            font-size: 1.8rem;
                            font-weight: 700;
                            color: #2d3748;
                            background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
                            -webkit-background-clip: text;
                            -webkit-text-fill-color: transparent;
                            background-clip: text;
                        }

                        p {
                            margin: 0.25rem 0 0 0;
                            color: #718096;
                            font-size: 0.9rem;
                        }
                    }
                }

                .platform-tag {
                    display: inline-flex;
                    align-items: center;
                    gap: 0.5rem;
                    padding: 0.5rem 1rem;
                    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
                    color: white;
                    border-radius: 25px;
                    font-size: 0.8rem;
                    font-weight: 500;
                    box-shadow: 0 4px 8px rgba(102, 126, 234, 0.3);
                }
            }

            .login-content {
                display: flex;
                gap: 3rem;
                align-items: flex-start;

                .login-form-section {
                    flex: 1;
                    min-width: 0;

                    h2 {
                        margin: 0 0 0.5rem 0;
                        font-size: 1.5rem;
                        font-weight: 600;
                        color: #2d3748;
                        text-align: center;
                    }

                    .login-subtitle {
                        text-align: center;
                        color: #718096;
                        margin-bottom: 2rem;
                        font-size: 0.9rem;
                    }

                    .form-group {
                        margin-bottom: 1.5rem;

                        label {
                            display: block;
                            margin-bottom: 0.5rem;
                            font-weight: 500;
                            color: #4a5568;
                            font-size: 0.9rem;
                        }

                        .input-wrapper {
                            position: relative;
                            display: flex;
                            align-items: center;

                            .input-icon {
                                position: absolute;
                                left: 1rem;
                                color: #a0aec0;
                                font-size: 1rem;
                                z-index: 2;
                            }

                            input {
                                width: 100%;
                                padding: 1rem 1rem 1rem 3rem;
                                border: 2px solid #e2e8f0;
                                border-radius: 12px;
                                font-size: 1rem;
                                transition: all 0.3s ease;
                                background: white;

                                &:focus {
                                    outline: none;
                                    border-color: #667eea;
                                    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
                                }

                                &::placeholder {
                                    color: #a0aec0;
                                }
                            }

                            .password-toggle {
                                position: absolute;
                                right: 1rem;
                                background: none;
                                border: none;
                                color: #a0aec0;
                                cursor: pointer;
                                font-size: 1rem;
                                transition: color 0.3s ease;

                                &:hover {
                                    color: #667eea;
                                }
                            }
                        }
                    }

                    .error-message {
                        background: #fed7d7;
                        color: #c53030;
                        padding: 0.75rem 1rem;
                        border-radius: 8px;
                        margin-bottom: 1rem;
                        font-size: 0.9rem;
                        border: 1px solid #feb2b2;
                    }

                    .login-button {
                        width: 100%;
                        padding: 1rem;
                        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
                        color: white;
                        border: none;
                        border-radius: 12px;
                        font-size: 1rem;
                        font-weight: 600;
                        cursor: pointer;
                        transition: all 0.3s ease;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        gap: 0.5rem;
                        box-shadow: 0 4px 8px rgba(102, 126, 234, 0.3);

                        &:hover:not(:disabled) {
                            transform: translateY(-2px);
                            box-shadow: 0 8px 16px rgba(102, 126, 234, 0.4);
                        }

                        &:disabled {
                            opacity: 0.7;
                            cursor: not-allowed;
                        }

                        &.loading {
                            .loading-spinner {
                                display: flex;
                                align-items: center;
                                gap: 0.5rem;

                                .spinner {
                                    width: 16px;
                                    height: 16px;
                                    border: 2px solid rgba(255, 255, 255, 0.3);
                                    border-top: 2px solid white;
                                    border-radius: 50%;
                                    animation: spin 1s linear infinite;
                                }
                            }
                        }
                    }

                    .login-footer {
                        margin-top: 2rem;
                        text-align: center;

                        p {
                            color: #a0aec0;
                            font-size: 0.8rem;
                            margin-bottom: 0.5rem;
                        }

                        .footer-links {
                            display: flex;
                            justify-content: center;
                            gap: 1rem;

                            a {
                                color: #667eea;
                                text-decoration: none;
                                font-size: 0.8rem;
                                transition: color 0.3s ease;

                                &:hover {
                                    color: #764ba2;
                                }
                            }
                        }
                    }
                }

                .demo-section {
                    flex: 0 0 320px;
                    padding: 2rem;
                    background: rgba(102, 126, 234, 0.05);
                    border-radius: 15px;
                    border: 1px solid rgba(102, 126, 234, 0.1);

                    h3 {
                        margin: 0 0 0.5rem 0;
                        font-size: 1.1rem;
                        font-weight: 600;
                        color: #2d3748;
                        text-align: center;
                    }

                    p {
                        text-align: center;
                        color: #718096;
                        margin-bottom: 1.5rem;
                        font-size: 0.9rem;
                    }

                    .demo-buttons {
                        display: flex;
                        flex-direction: column;
                        gap: 0.75rem;

                        .demo-btn {
                            padding: 0.75rem;
                            border: 2px solid #e2e8f0;
                            border-radius: 10px;
                            background: white;
                            color: #4a5568;
                            font-size: 0.8rem;
                            font-weight: 500;
                            cursor: pointer;
                            transition: all 0.3s ease;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            gap: 0.5rem;

                            &:hover {
                                transform: translateY(-1px);
                                box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
                            }

                            &.admin {
                                border-color: #667eea;
                                color: #667eea;

                                &:hover {
                                    background: #667eea;
                                    color: white;
                                }
                            }

                            &.manning {
                                border-color: #38a169;
                                color: #38a169;

                                &:hover {
                                    background: #38a169;
                                    color: white;
                                }
                            }

                            &.fleet {
                                border-color: #ed8936;
                                color: #ed8936;

                                &:hover {
                                    background: #ed8936;
                                    color: white;
                                }
                            }

                            &.medical {
                                border-color: #e53e3e;
                                color: #e53e3e;

                                &:hover {
                                    background: #e53e3e;
                                    color: white;
                                }
                            }

                            &.crew {
                                border-color: #9f7aea;
                                color: #9f7aea;

                                &:hover {
                                    background: #9f7aea;
                                    color: white;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

// Animations
@keyframes float {

    0%,
    100% {
        transform: translateY(0px);
    }

    50% {
        transform: translateY(-20px);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

// Responsive design
@media (max-width: 768px) {
    .login-page {
        .login-container {
            padding: 1rem;

            .login-card {
                padding: 2rem;

                .login-header {
                    .logo-section {
                        flex-direction: column;
                        gap: 0.5rem;

                        .logo-icon {
                            width: 50px;
                            height: 50px;
                            font-size: 1.5rem;
                        }

                        .logo-text {
                            h1 {
                                font-size: 1.5rem;
                            }
                        }
                    }
                }

                .login-content {
                    flex-direction: column;
                    gap: 2rem;

                    .login-form-section {
                        order: 1;

                        .demo-buttons {
                            grid-template-columns: 1fr;
                        }
                    }

                    .demo-section {
                        order: 2;
                        flex: none;
                        width: 100%;
                        padding: 1.5rem;

                        .demo-buttons {
                            display: grid;
                            grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
                            gap: 0.75rem;
                        }
                    }
                }
            }
        }
    }
}

// Mobile responsive styles for Login Page
@media (max-width: 768px) {
    .login-page {
        min-height: 100vh;
        padding: 1rem;

        .login-container {
            flex-direction: column;
            gap: 2rem;
            padding: 1.5rem;
            width: 100%;
            max-width: 400px;
            margin: 0 auto;

            .login-form-section {
                order: 1;
                width: 100%;

                .login-header {
                    text-align: center;
                    margin-bottom: 1.5rem;

                    h1 {
                        font-size: 1.75rem;
                        margin-bottom: 0.5rem;
                    }

                    p {
                        font-size: 0.875rem;
                    }
                }

                .login-form {
                    .form-group {
                        margin-bottom: 1rem;

                        .input-group {
                            .input-field {
                                padding: 0.875rem 1rem;
                                font-size: 1rem;
                            }

                            .password-toggle {
                                right: 0.75rem;
                                font-size: 1rem;
                            }
                        }
                    }

                    .login-button {
                        width: 100%;
                        padding: 0.875rem;
                        font-size: 1rem;
                        margin-top: 1rem;
                    }
                }
            }

            .demo-accounts-section {
                order: 2;
                width: 100%;

                h3 {
                    font-size: 1.25rem;
                    margin-bottom: 1rem;
                }

                .demo-accounts {
                    gap: 0.75rem;

                    .demo-account {
                        padding: 0.875rem;
                        font-size: 0.875rem;

                        .account-info {
                            .account-name {
                                font-size: 0.875rem;
                            }

                            .account-role {
                                font-size: 0.75rem;
                            }
                        }

                        .demo-login-btn {
                            padding: 0.5rem 0.75rem;
                            font-size: 0.75rem;
                        }
                    }
                }
            }
        }

        .floating-elements {
            .element {
                &.ship {
                    width: 60px;
                    height: 60px;
                    font-size: 2rem;
                }

                &.anchor {
                    width: 40px;
                    height: 40px;
                    font-size: 1.5rem;
                }

                &.compass {
                    width: 50px;
                    height: 50px;
                    font-size: 1.75rem;
                }
            }
        }
    }
}

// Tablet adjustments
@media (min-width: 769px) and (max-width: 1024px) {
    .login-page {
        .login-container {
            max-width: 600px;
            padding: 2rem;

            .login-form-section {
                .login-header {
                    h1 {
                        font-size: 2rem;
                    }
                }
            }

            .demo-accounts-section {
                .demo-accounts {
                    grid-template-columns: repeat(2, 1fr);
                }
            }
        }
    }
}

// Large screen optimizations
@media (min-width: 1025px) {
    .login-page {
        .login-container {
            max-width: 800px;
            padding: 3rem;

            .login-form-section {
                .login-header {
                    h1 {
                        font-size: 2.25rem;
                    }
                }
            }
        }
    }
}