@import '../variables';

.roles-permissions-page {
    .roles-permissions-content {
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }

    // Permission Matrix Section
    .permission-matrix-section {
        background: $color-white;
        border-radius: $border-radius;
        border: 1px solid $box-border-color;
        overflow: hidden;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);

        .section-header {
            padding: 1.5rem 2rem;
            border-bottom: 1px solid $box-border-color;
            background: $color-gray-light-4;

            h3 {
                margin: 0 0 0.5rem 0;
                color: $color-primary;
                font-weight: 600;
                font-size: 1.2rem;
            }

            p {
                margin: 0;
                color: $color-gray;
                font-size: 0.9rem;
            }
        }

        .matrix-container {
            overflow-x: auto;
            max-width: 100%;

            .matrix-table {
                min-width: 800px;

                .matrix-header {
                    display: grid;
                    grid-template-columns: 300px repeat(auto-fit, minmax(150px, 1fr));
                    background: $color-gray-light-3;
                    border-bottom: 1px solid $box-border-color;
                    position: sticky;
                    top: 0;
                    z-index: 10;

                    .permission-header {
                        padding: 1rem 1.5rem;
                        font-weight: 600;
                        color: $color-primary;
                        border-right: 1px solid $box-border-color;
                    }

                    .role-header {
                        padding: 1rem;
                        text-align: center;
                        border-right: 1px solid $box-border-color;

                        &:last-child {
                            border-right: none;
                        }

                        .role-name {
                            font-weight: 600;
                            color: $color-black;
                            margin-bottom: 0.25rem;
                            font-size: 0.9rem;
                        }

                        .role-users {
                            font-size: 0.8rem;
                            color: $color-gray;
                            background: $color-white;
                            padding: 0.2rem 0.5rem;
                            border-radius: 12px;
                            display: inline-block;
                        }
                    }
                }

                .matrix-body {
                    .permission-section {
                        border-bottom: 1px solid $box-border-color;

                        &:last-child {
                            border-bottom: none;
                        }

                        .section-title {
                            display: flex;
                            align-items: center;
                            gap: 0.5rem;
                            padding: 1rem 1.5rem;
                            background: $color-gray-light-4;
                            font-weight: 600;
                            color: $color-primary;
                            font-size: 0.9rem;
                            border-bottom: 1px solid $box-border-color;

                            svg {
                                font-size: 1rem;
                            }
                        }

                        .permission-row {
                            display: grid;
                            grid-template-columns: 300px repeat(auto-fit, minmax(150px, 1fr));
                            border-bottom: 1px solid $color-gray-light-2;
                            transition: background 0.2s;

                            &:hover {
                                background: $color-gray-light-4;
                            }

                            &:last-child {
                                border-bottom: none;
                            }

                            .permission-info {
                                padding: 1rem 1.5rem;
                                border-right: 1px solid $box-border-color;

                                .permission-name {
                                    font-weight: 500;
                                    color: $color-black;
                                    margin-bottom: 0.25rem;
                                    font-size: 0.9rem;
                                }

                                .permission-description {
                                    font-size: 0.8rem;
                                    color: $color-gray;
                                    line-height: 1.3;
                                }
                            }

                            .permission-checkbox {
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                padding: 1rem;
                                border-right: 1px solid $box-border-color;

                                &:last-child {
                                    border-right: none;
                                }

                                input[type="checkbox"] {
                                    display: none;
                                }

                                label {
                                    width: 20px;
                                    height: 20px;
                                    border: 2px solid $color-gray-light-2;
                                    border-radius: 4px;
                                    cursor: pointer;
                                    position: relative;
                                    transition: all 0.2s;
                                    background: $color-white;

                                    &:hover {
                                        border-color: $color-primary;
                                        background: rgba($color-primary, 0.05);
                                    }

                                    &::after {
                                        content: '✓';
                                        position: absolute;
                                        top: 50%;
                                        left: 50%;
                                        transform: translate(-50%, -50%);
                                        color: $color-white;
                                        font-size: 12px;
                                        font-weight: bold;
                                        opacity: 0;
                                        transition: opacity 0.2s;
                                    }
                                }

                                input[type="checkbox"]:checked+label {
                                    background: $color-primary;
                                    border-color: $color-primary;

                                    &::after {
                                        opacity: 1;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    // Roles Management Section
    .roles-management-section {
        background: $color-white;
        border-radius: $border-radius;
        border: 1px solid $box-border-color;
        overflow: hidden;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);

        .section-header {
            padding: 1.5rem 2rem;
            border-bottom: 1px solid $box-border-color;
            background: $color-gray-light-4;

            h3 {
                margin: 0 0 0.5rem 0;
                color: $color-primary;
                font-weight: 600;
                font-size: 1.2rem;
            }

            p {
                margin: 0;
                color: $color-gray;
                font-size: 0.9rem;
            }
        }

        .roles-table {
            overflow-x: auto;

            table {
                width: 100%;
                border-collapse: collapse;

                thead {
                    background: $color-gray-light-3;

                    th {
                        padding: 1rem 1.5rem;
                        text-align: left;
                        font-weight: 600;
                        color: $color-primary;
                        font-size: 0.9rem;
                        border-bottom: 1px solid $box-border-color;
                    }
                }

                tbody {
                    tr {
                        border-bottom: 1px solid $color-gray-light-2;
                        transition: background 0.2s;

                        &:hover {
                            background: $color-gray-light-4;
                        }

                        &:last-child {
                            border-bottom: none;
                        }

                        td {
                            padding: 1rem 1.5rem;
                            vertical-align: middle;

                            .role-info {
                                .role-name {
                                    font-weight: 500;
                                    color: $color-black;
                                    margin-bottom: 0.25rem;
                                }
                            }

                            .user-count {
                                background: $color-primary;
                                color: $color-white;
                                padding: 0.2rem 0.6rem;
                                border-radius: 12px;
                                font-size: 0.8rem;
                                font-weight: 500;
                            }

                            .role-actions {
                                display: flex;
                                gap: 0.5rem;

                                .btn-root {
                                    padding: 0.3rem 0.7rem;
                                    font-size: 0.8rem;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    // Responsive adjustments
    @media (max-width: 1200px) {
        .permission-matrix-section {
            .matrix-container {
                .matrix-table {

                    .matrix-header,
                    .permission-row {
                        grid-template-columns: 250px repeat(auto-fit, minmax(120px, 1fr));
                    }
                }
            }
        }
    }

    @media (max-width: 768px) {
        .permission-matrix-section {
            .matrix-container {
                .matrix-table {

                    .matrix-header,
                    .permission-row {
                        grid-template-columns: 200px repeat(auto-fit, minmax(100px, 1fr));
                    }

                    .matrix-header {
                        .role-header {
                            .role-name {
                                font-size: 0.8rem;
                            }

                            .role-users {
                                font-size: 0.7rem;
                            }
                        }
                    }

                    .permission-row {
                        .permission-info {
                            .permission-name {
                                font-size: 0.8rem;
                            }

                            .permission-description {
                                font-size: 0.75rem;
                            }
                        }
                    }
                }
            }
        }

        .roles-management-section {
            .roles-table {
                table {

                    thead th,
                    tbody td {
                        padding: 0.75rem 1rem;
                        font-size: 0.85rem;
                    }
                }
            }
        }
    }
}